import React from "react";

const Services = ({ classicHeader, darkTheme }) => {
   // services details
   const services = [
      {
         name: "Resonsive Email Template",
         desc: "Create visually appealing and mobile-friendly email templates that render well across different email clients and devices.",
         icon: "fas fa-mobile-screen",
      },
      {
         name: "Web Development",
         desc: "Develop custom websites and web applications using modern technologies and best practices, ensuring a seamless user experience with optimized performance.",
         icon: "fas fa-globe",
      },
      {
         name: "HTML Email Development",
         desc: "Convert designs into high-quality HTML email templates, ensuring compatibility and consistency across various email platforms.",
         icon: "fa-regular fa-envelope",
      },
      {
         name: "Front-end Development",
         desc: "Implement responsive and interactive front-end designs, utilizing HTML, CSS, and JavaScript to create engaging user interfaces and great user experiences.",
         icon: "fas fa-laptop",
      },
      {
         name: "Comprehensive Email Testing",
         desc: "Email testing to ensure content displayed correctly across different email clients, devices, and browsers. This includes testing for visual rendering and link functionality.",
         icon: "fa-regular fa-square-check",
      },
      {
         name: "Website Maintenance and Support",
         desc: "Provide ongoing maintenance and support to keep websites updated, secure, and functioning smoothly. This includes content updates, bug fixes, and performance.",
         icon: "fas fa-screwdriver-wrench",
      },
   ];

   return (
      <section id="services" className={"section " + (darkTheme ? "bg-dark-2" : "bg-light")}>
         <div className={"container " + (classicHeader ? "" : "px-lg-5")}>
            {/* Heading */}
            <div className="position-relative d-flex text-center mb-5">
               <h2
                  className={
                     "text-24  text-uppercase fw-600 w-100 mb-0 " +
                     (darkTheme ? "text-white-50  opacity-1" : "text-light  opacity-4")
                  }
               >
                  Services
               </h2>
               <p
                  className={
                     "text-9  fw-600 position-absolute w-100 align-self-center lh-base mb-0 " +
                     (darkTheme ? "text-white" : "text-dark")
                  }
               >
                  What I Do?
                  <span className="heading-separator-line border-bottom border-3 border-primary d-block mx-auto" />
               </p>
            </div>
            {/* Heading end*/}
            {/* content start */}
            <div className="row">
               <div className="col-lg-11 mx-auto">
                  <div className="row">
                     {services.length > 0 &&
                        services.map((service, index) => (
                           <div className="col-md-6" key={index}>
                              <div className="featured-box style-3 mb-5">
                                 <div
                                    className={
                                       "featured-box-icon text-primary  shadow-sm rounded " +
                                       (darkTheme ? "bg-dark-1" : "bg-white")
                                    }
                                 >
                                    <i className={service.icon} />
                                 </div>
                                 <h3 className={darkTheme ? "text-white" : ""}>{service.name}</h3>
                                 <p className={"mb-0 " + (darkTheme ? "text-white-50" : "")}>
                                    {service.desc}
                                 </p>
                              </div>
                           </div>
                        ))}
                  </div>
               </div>
            </div>
            {/* content end */}
         </div>
      </section>
   );
};

export default Services;
